<template>

    <section class="image-preview-container" v-if="isVisible">
        <transition-group name="fade" >

            <LoadingSpinner class="center" v-if="isLoading" key="loadingSpinner"/>
            <!--v-bind:style="{'height': height === -1 ? '70%': height + '%'}"-->

            <div class="content-wrapper" key="preview" v-if="!isLoading"
                 v-bind:style="{
                    height: !!lastData.height === false ? '70%' : (lastData.height + '%'),
                    flexDirection: imageRatioAspec > 2 ? 'column' : 'row'
                 }"

                 v-bind:class="{'horizontal': imageRatioAspec && imageRatioAspec >= 1.5}"
            >

                <img ref="img" />
                <p v-if="text.length !== 0">{{text}}</p>
            </div>

        </transition-group>

    </section>
</template>

<script>
    import {Events} from "../../common/events";
    import imageFetch from "../../common/imageFetch"
    import LoadingSpinner from "./LoadingSpinner";

    export default {
        name: "ImagePreviewContainer",

        components: {LoadingSpinner},

        data: () => {
            return {
                isVisible: false,
                isLoading: false,

                naturalWidth: -1,
                naturalHeight: -1,

                text: "",
                lastData: null,
            }
        },

        methods: {
            async showImage(data) {
                this.isLoading = true;
                this.lastData = data;
                this.isVisible = true;

                this.$eventBus.$emit(Events.showUnfocusedBackground, {zIndex: 16});
                this.$eventBus.$on(Events.onBackgroundClick, () => {
                    this.hide();
                });
                //window.addEventListener('scroll', this.hide);

                let res = await this.downloadImage(data.img);
                this.isLoading = false;

                this.$nextTick(() => {
                    this.$refs.img.src = res;
                    //console.log({img: this.$refs.img});

                    this.$nextTick(() => {
                        this.naturalWidth = this.$refs.img.naturalWidth;
                        this.naturalHeight = this.$refs.img.naturalHeight;
                    });
                });

                if (data.text) {
                    this.text = await data.text();
                }
            },

            async downloadImage(src) {
                let rawRes = await imageFetch.download(src);
                return imageFetch.parse(await rawRes.arrayBuffer());
            },

            show() {
                this.isVisible = true;
            },

            hide() {
                if (!this.isVisible) return;

                if (!!this.lastData && !this.lastData.hasOwnProperty("notHideBackgroundOnClick")) {
                    this.$eventBus.$emit(Events.hideUnfocusedBackground);
                }

                this.$eventBus.$off(Events.onBackgroundClick);
                //window.removeEventListener('scroll', this.hide);
                this.isVisible = false;
                this.lastData = null;
                this.text = "";
            },
        },

        computed: {
            imageRatioAspec: function () {
                return this.naturalWidth / this.naturalHeight
            }
        },

        created() {
            this.$eventBus.$on(Events.showImagePreview, (data) => this.showImage(data));
        },

        destroyed() {
            this.$eventBus.$off(Events.showImagePreview);
        },
    }
</script>

<style lang="sass" scoped>

    .fade-enter-active, .fade-leave-active
        transition: 0.35s

    .fade-enter, .fade-leave-to
        opacity: 0

    .image-preview-container
        position: absolute
        z-index: 17
        top: 0
        left: 0
        right: 0
        bottom: 0
        overflow: hidden
        pointer-events: none

        .single-img
            position: fixed
            left: 50%
            top: 50%
            transform: translate(-50%, -50%)

        .horizontal
            display: block !important
            width: 50% !important
            height: auto !important

            img
                width: 100% !important
                height: auto !important

            p
                max-height: 200px
                width: auto

        .content-wrapper
            display: flex
            justify-content: center
            position: fixed
            top: 50%
            left: 50%
            transform: translate(-50%, -50%)
            width: 100%
            text-align: center
            //box-shadow: 0 14px 28px rgba(0, 0, 0, 0.52), 0 10px 10px rgba(0, 0, 0, 0.5)

            img
                height: 100%
                width: auto

            p
                max-width: 200px
                padding: 15px
                margin: 0
                overflow-y: auto
                word-break: break-word
                font-size: 1rem
                line-height: 1.3
                color: black
                background-color: #FFFFFF
                pointer-events: bounding-box

        .center
            margin: auto
            position: fixed
            top: 0
            left: 0
            bottom: 0
            right: 0
            z-index: 17
</style>